import React, {FC, useEffect} from 'react'
import {Box, Button, Container, CssBaseline, Grid, Paper, Typography} from "@mui/material";
import Header from "../../../components/header";
import {useSelector} from "react-redux";
import {DeliverySelector} from "../../../store/selectors/selectors";
import {useNavigate} from "react-router-dom";
import Copyright from "../../../components/copyright";
import dayjs from "dayjs";

const DeliveryCheck: FC = () => {

    const {
        step,
        to,
        type,
        ready,
        date,
        appNumber,
        fromLastName,
        fromFirstName,
        fromPhone,
        fromExtraPhone,
        fromAddress,
        fromExtraAddress,
        fromCompanyName,
        fromPickUpTimeMin,
        fromPickUpTimeMax,
        fromDocuments,
        nature,
        natureDescription,
        toLastName,
        toFirstName,
        toPhone,
        toExtraPhone,
        toAddress,
        toExtraAddress,
        toCompanyName,
        toPickUpTimeMin,
        toPickUpTimeMax,
        toDocuments,
        needPass,
        call
    }
        = useSelector(DeliverySelector)

    const navigate = useNavigate()

    useEffect(() => {
        if (step < 7) {
            navigate('delivery')
        }
    }, [])

    return (
        <>
            <CssBaseline/>
            <Header/>
            <Container component="main" maxWidth="md" sx={{mb: 4}}>
                <Paper variant="outlined" sx={{my: {xs: 3, md: 6}, p: {xs: 2, md: 3}}}>
                    <Typography component="h1" variant="h4" align="center">
                         
                    </Typography>
                    <Grid container spacing={3}>
                        <Grid item xs={12} sm={6}>
                            <Typography variant="h6" align="center">
                                 
                            </Typography>
                            <Typography variant="subtitle1">
                                 : {to}
                            </Typography>
                            <Typography variant="subtitle1">
                                 : {type}
                            </Typography>
                            <Typography variant="subtitle1">
                                 : {appNumber}
                            </Typography>
                            <Typography variant="subtitle1">
                                 : {dayjs(date).format('DD.MM.YYYY')}
                            </Typography>
                            <Typography variant="subtitle1">
                                 : {ready}
                            </Typography>
                            <Typography variant="subtitle1">
                                : {fromLastName}
                            </Typography>
                            <Typography variant="subtitle1">
                                : {fromFirstName}
                            </Typography>
                            <Typography variant="subtitle1">
                                 : {fromPhone}
                            </Typography>
                            <Typography variant="subtitle1">
                                .  : {fromExtraPhone}
                            </Typography>
                            <Typography variant="subtitle1">
                                : {fromAddress}
                            </Typography>
                            <Typography variant="subtitle1">
                                . : {fromExtraAddress}
                            </Typography>
                            <Typography variant="subtitle1">
                                  : {dayjs(fromPickUpTimeMin).format('HH : mm')}
                            </Typography>
                            <Typography variant="subtitle1">
                                  : {dayjs(fromPickUpTimeMax).format('HH : mm')}
                            </Typography>
                            <Typography variant="subtitle1">
                                 : {fromCompanyName}
                            </Typography>
                            <Typography variant="subtitle1">
                                : {fromDocuments}
                            </Typography>
                            <Typography variant="subtitle1">
                                  : {nature}
                            </Typography>
                            <Typography variant="subtitle1">
                                 : {natureDescription}
                            </Typography>
                            <Typography variant="subtitle1">
                                  : {needPass}
                            </Typography>
                            <Typography variant="subtitle1">
                                   : {call}
                            </Typography>
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <Typography variant="h6" align="center">
                                  
                            </Typography>
                            <Typography variant="subtitle1">
                                : {toLastName}
                            </Typography>
                            <Typography variant="subtitle1">
                                : {toFirstName}
                            </Typography>
                            <Typography variant="subtitle1">
                                 : {toPhone}
                            </Typography>
                            <Typography variant="subtitle1">
                                .  : {toExtraPhone}
                            </Typography>
                            <Typography variant="subtitle1">
                                : {toAddress}
                            </Typography>
                            <Typography variant="subtitle1">
                                . : {toExtraAddress}
                            </Typography>
                            <Typography variant="subtitle1">
                                  : {dayjs(toPickUpTimeMin).format('HH : mm')}
                            </Typography>
                            <Typography variant="subtitle1">
                                  : {dayjs(toPickUpTimeMax).format('HH : mm')}
                            </Typography>
                            <Typography variant="subtitle1">
                                 : {toCompanyName}
                            </Typography>
                            <Typography variant="subtitle1">
                                : {toDocuments}
                            </Typography>
                        </Grid>
                    </Grid>
                    <Box sx={{display: 'flex', justifyContent: 'space-between', mt: 3}}>
                        <Button>
                            
                        </Button>
                        <Button variant="contained" type="submit">
                            
                        </Button>
                    </Box>
                </Paper>
                <Copyright/>
            </Container>
        </>
    )
}

export default DeliveryCheck
